import { Component, OnInit } from '@angular/core';
import { AdminService } from 'src/app/service/admin.service';
import { Company } from 'src/app/models/company';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-company',
  templateUrl: './add-company.component.html',
  styleUrls: ['./add-company.component.css']
})
export class AddCompanyComponent implements OnInit {

  public comp: Company= new Company;
  public loggedInType: String;

  constructor(public adminService: AdminService, public router:Router) { }

  ngOnInit() {
    this.loggedInType=sessionStorage.type;
  }


  createCompany(){
    if (this.comp.email!=null && this.comp.name!=null && this.comp.password!=null) {
      
   
    this.adminService.addCompany(this.comp).subscribe(compFromServer=>{
      alert("The Company: "+compFromServer.name+" with id: " +compFromServer.id + " was created")
    }, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
      
    }})
  }else alert("Please Fill All Required Details!");
}
}
