import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { NavComponent } from './components/nav/nav.component';
import { AdminComponent } from './components/admin/admin.component';
import { CompaniesComponent } from './components/companies/companies.component';
import { CustomersComponent } from './components/customers/customers.component';
import { CouponsComponent } from './components/coupons/coupons.component';
import { AddCompanyComponent } from './components/add-company/add-company.component';
import { AddCustomerComponent } from './components/add-customer/add-customer.component';
import { AddCouponComponent } from './components/add-coupon/add-coupon.component';
import { CompanyCouponsComponent } from './components/company-coupons/company-coupons.component';
import { MyCouponsComponent } from './components/my-coupons/my-coupons.component';


const routes: Routes = [
  {path: "admin", component: AdminComponent},
  {path: "companies", component: CompaniesComponent},
  {path: "customers", component: CustomersComponent},
  {path: "coupons", component: CouponsComponent},
  {path: "myCoupons", component: MyCouponsComponent},
  {path: "companyCoupons", component: CompanyCouponsComponent},
  {path: "addCompany", component: AddCompanyComponent},
  {path: "addCustomer", component: AddCustomerComponent},
  {path: "addCoupon", component: AddCouponComponent},
  { path: '',   component: LoginComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
