import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Company } from '../models/company';
import { Customer } from '../models/customer';
import { Coupon } from '../models/coupon';

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  constructor(public httpClient: HttpClient) {}
 
  public showAllCompanies(): Observable<Company[]>{
    return this.httpClient.get<Company[]>("http://localhost:8080/admin/company/"+sessionStorage.token)
  }

  public showAllCustomers(): Observable<Customer[]>{
    return this.httpClient.get<Customer[]>("http://localhost:8080/admin/customer/"+sessionStorage.token)
  }

  public showAllCoupons(): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/admin/allCoupons/"+sessionStorage.token)
  }

  public showCouponsbyCategory(category: String): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/admin/coupon/category/"+sessionStorage.token+"/"+ category)
  }

  public showCouponsbyMaxPrice(max: number): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/admin/coupon/price/"+sessionStorage.token+"/"+ max)
  }

  public addCompany(comp: Company): Observable<Company>{
    return this.httpClient.post<Company>("http://localhost:8080/admin/company/"+sessionStorage.token, comp)
  }

  public addCustomer(cust: Customer): Observable<Customer>{
    return this.httpClient.post<Customer>("http://localhost:8080/admin/customer/"+sessionStorage.token, cust)
  }

  public deleteCustomer(id: number): Observable<any>{
    return this.httpClient.delete("http://localhost:8080/admin/customer/"+sessionStorage.token+ "/" + id)
  }

  public deleteCompany(id: number): Observable<any>{
    return this.httpClient.delete("http://localhost:8080/admin/company/"+sessionStorage.token+ "/" + id)
  }

  public editCompany(comp: Company): Observable<Company>{
    return this.httpClient.put<Company>("http://localhost:8080/admin/company/"+sessionStorage.token, comp)
  }

  public editCustomer(cust: Customer): Observable<Customer>{
    return this.httpClient.put<Customer>("http://localhost:8080/admin/customer/"+sessionStorage.token, cust)
  }

}

