import { Component, OnInit } from '@angular/core';
import { Customer } from 'src/app/models/customer';
import { AdminService } from 'src/app/service/admin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.css']
})
export class CustomersComponent implements OnInit {

  public c: Customer;
  public editor: boolean=false;
  public customers: Customer[];
  public loggedInType: String;


  constructor(public adminService: AdminService, public router:Router ) { }

  ngOnInit() {

    this.loggedInType=sessionStorage.type;

    this.showAllCustomers();
    
  }

  public showAllCustomers(){
    this.adminService.showAllCustomers().subscribe((cust)=>{
      this.customers=cust;
    }, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
    }})
  }

  public deleteCustomer(id: number){
    this.adminService.deleteCustomer(id).subscribe(()=>
    {this.ngOnInit();}, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
    }})
    
}

public editorEnabler(cust:Customer){
  this.c= cust;
  if (this.editor==false) {
    this.editor=true;
  }else this.editor=false;

}

public updateCustomer(){
  if (this.c.email!=null && this.c.firstName!=null && this.c.lastName!=null && this.c.password!=null) {
    
 
  this.adminService.editCustomer(this.c).subscribe(custFromServer=>{
    alert("The Customer: "+this.c.firstName+ " " +this.c.lastName+ " was updated")
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})
}else alert("Please Fill All Required Details!");
}

}
