import { Component, OnInit, Input } from '@angular/core';
import { LoginService } from '../../service/login.service';
import { Router } from '@angular/router';
import { NumberValueAccessor } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private userName:string;
  private password: string;
  private type: string;
  private id: Number;
  
  

  constructor(private loginService: LoginService, private router: Router){}
  ngOnInit() {
    if (sessionStorage.type) {
      this.routeToUser(sessionStorage.type);
    }
  }
  public login():void{
    this.loginService.login(this.userName, this.password, this.type).subscribe(response =>{
      if(response.token){
              //alert(response.token);
      sessionStorage.token = response.token;
      sessionStorage.type= this.type;
      this.routeToUser(sessionStorage.type);

      }
},error=>{alert("Wrong Password/UserName/Type")});
}
routeToUser(type) {
  switch (type) {
    case "admin":
      sessionStorage.type = "admin";
      sessionStorage.loggedIn== true;
      this.router.navigate(["/admin"]);
      break;
    case "company":
      sessionStorage.type = "company";
      sessionStorage.loggedIn== true;
      this.router.navigate(["/companyCoupons"]);
      break;
    case "customer":
      sessionStorage.type = "customer";
      sessionStorage.loggedIn== true;
      this.router.navigate(["/myCoupons"]);
  }
}
}
