import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Coupon } from '../models/coupon';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  constructor(public httpClient: HttpClient) { }


  public showCustomerCoupons(): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/customer/coupon/"+sessionStorage.token);
  }

  public purchaseCoupon(coupon: Coupon): Observable<Coupon>{
    return this.httpClient.post<Coupon>("http://localhost:8080/customer/coupon/"+sessionStorage.token, coupon); 
  }

}


