import { Component, OnInit } from '@angular/core';
import { AdminService} from 'src/app/service/admin.service';
import { Company } from 'src/app/models/company';
import { Router } from '@angular/router';

@Component({
  selector: 'app-companies',
  templateUrl: './companies.component.html',
  styleUrls: ['./companies.component.css']
})
export class CompaniesComponent implements OnInit {

  public c: Company;
  public companies: Company[];
  public editor: boolean=false;
public loggedInType: String;
  constructor( public adminService: AdminService, public router: Router) { }

  ngOnInit() {
    this.loggedInType=sessionStorage.type;

    this.showAllCompanies();
  
 
  }

  public deleteCompany(id: number){
    this.adminService.deleteCompany(id).subscribe(()=>{this.ngOnInit();}, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
    }})
    
}

public editorEnabler(comp:Company){
  this.c= comp;
  if (this.editor==false) {
    this.editor=true;
  }else this.editor=false;

}

public updateCompany(){
  if (this.c.email!=null && this.c.name!=null && this.c.password!=null) {
      
  this.adminService.editCompany(this.c).subscribe(compFromServer=>{
    alert("The Company: "+this.c.name+ " was updated")
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})
}else alert("Please Fill All Required Details!");
}



public showAllCompanies(){
  this.adminService.showAllCompanies().subscribe((comp)=>{
    this.companies=comp;
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})  
}
}
