import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CustomerService } from 'src/app/service/customer.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-my-coupons',
  templateUrl: './my-coupons.component.html',
  styleUrls: ['./my-coupons.component.css']
})
export class MyCouponsComponent implements OnInit {

  public coupon:Coupon;
  public customerCoupons:Coupon[];
  public loggedInType:String;

  constructor(public customerService:CustomerService, public router:Router) { }

  ngOnInit() {

    this.loggedInType=sessionStorage.type;

   this.showCustomerCoupons();
  }

  public showCustomerCoupons(){
    this.customerService.showCustomerCoupons().subscribe((coup)=>{
      this.customerCoupons=coup;
    }, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
    }})
  }

}
