import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { CompanyService } from 'src/app/service/company.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-company-coupons',
  templateUrl: './company-coupons.component.html',
  styleUrls: ['./company-coupons.component.css']
})
export class CompanyCouponsComponent implements OnInit {

  public coupon: Coupon;
  public coupons: Coupon[];
  public couponsCategory: String;
  public maxPrice:number;
  public editor: boolean=false;
  public couponsFilter: String="all";
  public loggedInType:String;



  constructor(public companyService: CompanyService, public router:Router) { }

  ngOnInit() {
    this.loggedInType=sessionStorage.type;

    this.showCompanyCoupons();

  }

public showCompanyCoupons(){
  this.companyService.showCompanyCoupons().subscribe((coup)=>{
    this.coupons=coup;
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})
}

public showCompanyCouponsByCategory(){
  this.companyService.showCompanyCouponsbyCategory(this.couponsCategory).subscribe((coup)=>{
    this.coupons=coup;
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})
}

public showCompanyCouponsByMaxPrice(){
  this.companyService.showCouponsbyMaxPrice(this.maxPrice).subscribe((coup)=>{
    this.coupons= coup;
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})
}

public deleteCompanyCoupon(id: number){
  this.companyService.deleteCoupon(id).subscribe(()=>{this.showCompanyCoupons();
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})  
}

public updateCompanyCoupon(){
  if (this.coupon.amount!=null && this.coupon.categoryName!=null && this.coupon.description!=null &&
    this.coupon.endDate!=null && this.coupon.image!=null && this.coupon.price!=null &&
     this.coupon.startDate!=null && this.coupon.title!=null ) {
    
  this.companyService.editCoupon(this.coupon).subscribe(coupFromServer=>{
    alert("The Coupon: "+coupFromServer.title+ " was updated");
  }, error=>{if (error.error=="Session Timeout") {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("type");
    this.router.navigate(['']);
  }})
} else alert("Please Fill All Required Details!");
}


public editorEnabler(coup:Coupon){
  this.coupon= coup;
  if (this.editor==false) {
    this.editor=true;
  }else this.editor=false;
}



}


