import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../models/coupon';
import { Observable } from 'rxjs';
import { interval } from 'rxjs'


@Injectable({
  providedIn: 'root'
})
export class CompanyService {

  constructor(public httpClient: HttpClient) { }




  public addCoupon(coup: Coupon): Observable<Coupon>{
    return this.httpClient.post<Coupon>("http://localhost:8080/company/coupon/"+sessionStorage.token, coup)
  }

  public showCompanyCoupons(): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/company/coupon/"+sessionStorage.token)
  }

  public showCompanyCouponsbyCategory(category: String): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/company/coupon/category/"+sessionStorage.token+"/"+ category)
  }

  public showCouponsbyMaxPrice(max: number): Observable<Coupon[]>{
    return this.httpClient.get<Coupon[]>("http://localhost:8080/company/coupon/maxprice/"+sessionStorage.token+"/"+ max)
  }

  public deleteCoupon(id: number): Observable<any>{
    return this.httpClient.delete("http://localhost:8080/company/coupon/"+sessionStorage.token+ "/" + id)
  }

  public editCoupon(coup: Coupon): Observable<Coupon>{
    return this.httpClient.put<Coupon>("http://localhost:8080/company/coupon/"+sessionStorage.token, coup)
  }

  

}
