import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Customer } from '../models/customer';
import { Company } from '../models/company';

@Injectable({
  providedIn: 'root'
})
export class LoggerInformationService {

  constructor(public httpClient: HttpClient) { }


public showCustomerInformation(): Observable<Customer>{
  return this.httpClient.get<Customer>("http://localhost:8080/customer/"+sessionStorage.token)
}

public showCompanyInformation(): Observable<Company>{
  return this.httpClient.get<Company>("http://localhost:8080/company/"+sessionStorage.token)
}

}