import { Component, OnInit } from '@angular/core';
import { Company } from 'src/app/models/company';
import { Customer } from 'src/app/models/customer';
import { LoggerInformationService } from 'src/app/service/logger-information.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-logger-information',
  templateUrl: './logger-information.component.html',
  styleUrls: ['./logger-information.component.css']
})
export class LoggerInformationComponent implements OnInit {

  public customerLoggedIn: Customer;
  public companyLoggedIn: Company;
  public loggedInType: String;
  public gotInfo:boolean=false;

  constructor(public loggerInfoService: LoggerInformationService, public router: Router) { }

    ngOnInit(){
      if (sessionStorage.type) {
        this.loggedInType=sessionStorage.type;
      }
      
    }

    ngDoCheck(){
      if (sessionStorage.type && this.gotInfo==false) {
        this.loggedInType=sessionStorage.type;
        if (this.loggedInType=="company") {
          this.loggerInfoService.showCompanyInformation().subscribe((comp)=>{
            this.companyLoggedIn=comp;
            this.gotInfo=true;
           
        
          })
        }
        else if (this.loggedInType=="customer") {
          this.loggerInfoService.showCustomerInformation().subscribe((cust)=>{
            this.customerLoggedIn=cust;
            this.gotInfo=true;
        
          })
      }
      }

      

  
} 
public logout(){
  sessionStorage.removeItem("token");
  sessionStorage.removeItem("type");
  this.gotInfo=false;
  this.loggedInType=undefined;
  this.companyLoggedIn=undefined;
  this.customerLoggedIn=undefined;
  this.router.navigate(['']);
  
}

}



