import { Component, OnInit } from '@angular/core';
import { Coupon } from 'src/app/models/coupon';
import { AdminService } from 'src/app/service/admin.service';
import { CustomerService } from 'src/app/service/customer.service';
import { Router } from '@angular/router';
import { $ } from 'protractor';

@Component({
  selector: 'app-coupons',
  templateUrl: './coupons.component.html',
  styleUrls: ['./coupons.component.css']
})
export class CouponsComponent implements OnInit {
public editorPurchase: String;
  public coupon: Coupon;
  public coupons: Coupon[];
  public userType: String;
  public editor: boolean=false;
  public couponsFilter: String="all";
  public couponsCategory: String;
  public maxPrice:number;

  

  constructor(public adminService: AdminService, public customerService: CustomerService, public router:Router) { }

  ngOnInit() {

    this.userType=sessionStorage.type;
this.showAllCoupons();
  }

  

  
  
 

  public editorEnabler(coup:Coupon){
    this.coupon= coup;
    if (this.editor==false) {
      this.editor=true;

  
    }else this.editor=false;
  
  }

  public purchaseCoupon(coupon:Coupon){
    this.customerService.purchaseCoupon(coupon).subscribe(coupFromServer=>{
      alert("The Coup: "+this.coupon.title+ " "+ this.coupon.description+ " was purchased")
    }, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
    }else alert("Coupon already been purchased!")})
  }

  public showAllCoupons(){
    this.adminService.showAllCoupons().subscribe((coup)=>{
      this.coupons=coup;
      }, error=>{if (error.error=="Session Timeout") {
        sessionStorage.removeItem("token");
        sessionStorage.removeItem("type");
        this.router.navigate(['']);
      }})
  }


  public showCouponsByCategory(){
    console.log(this.couponsCategory)
    this.adminService.showCouponsbyCategory(this.couponsCategory).subscribe((coup)=>{
      this.coupons=coup;
    }, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
    }})
  }

  public showCouponsByMaxPrice(){
    this.adminService.showCouponsbyMaxPrice(this.maxPrice).subscribe((coup)=>{
      this.coupons=coup;
    }, error=>{if (error.error=="Session Timeout") {
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("type");
      this.router.navigate(['']);
    }})
  }


}
